import React, { useMemo } from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { NavLink } from 'react-router-dom';
import Slider from "react-slick";
import HelmetContent from 'helpers/HelmetContent';

import {
  Icon,
  Text,
  Paper,
  BlogCard,
  InfoCard,
  ImageContent,
  TeamMemberCard,
} from 'components';

import { useMount } from 'utils';
import {
  fetchCourses,
  fetchStudents,
} from 'actions';
import { useTranslator } from 'utils/translator';

import {
  academy,
} from "assets/academyImages";

import {
  academyImage,
  academyBoyImage,
  academyGirlImage,
  academyImageDark,
} from 'assets/pages';

import carouselSettings from './sliderSetting';

import './style.scss'

const mapStateToProps = ({
  courses,
  darkMode,
  students,
}) => ({
  courses,
  darkMode,
  students,
});

const Academy = ({
  courses,
  darkMode,
  students,
  fetchCourses,
  fetchStudents,
}) => {

  const { t } = useTranslator();

  useMount(() => {
    fetchCourses();
    fetchStudents();
  });

  const successedStudents = useMemo(() => {
    if(!students || !students.data || !students.data || !students.data.result || !students.data.result.length) return null;

    return students.data.result.map(item => (
      <Paper className="carouselItem" key={item._id}>
        <TeamMemberCard
          img={item.image_url}
          name={item.name}
          surname={item.surname}
          profession={item.profession}
          place={item.place}
          darkMode={darkMode}
        />
      </Paper>
    ));
  }, [students, darkMode]);


  const academyCourses = useMemo(() => {
    if(!courses || !courses.data || !courses.data || !courses.data.result || !courses.data.result.length) return null;

    return courses.data.result.map(item => (
      <Paper key={item._id} className='academyItem'>
        <NavLink to={`/academy/course/${item._id}`}>
          <BlogCard
            darkMode={darkMode}
            title={item.name}
            description={item.description}
            src={item.image_url}
            width="280px"
          />
        </NavLink>
      </Paper>
    ));
  }, [courses, darkMode]);

  return (
    <Paper className={classnames("Academy")}>
       <HelmetContent page="academy"/>
       <ImageContent
          path={academyImage}
          before={academyGirlImage}
          after={academyBoyImage}
          darkPath={academyImageDark}
          darkMode={darkMode}
        />
      <Paper className='page-content'>
        <Paper flexName='flexible jCenter'>
          <Text darkMode={darkMode} className='doubleExtraLarge academyTitle'>{ t("_Choose_your_course_")} </Text>
        </Paper>
        <Paper className='courseBlock' flexName='flexible wrap jCenter'>
          {academyCourses}
        </Paper>
        <Paper className='academyTextBlock'>
          <Paper flexName='flexible jCenter'>
            <Text darkMode={darkMode} className='doubleExtraLarge academyTitle'> { t("_Get_career_")}</Text>
          </Paper>
          <Paper>
            <InfoCard
              img={ academy }
              padding='0px'
              darkMode={darkMode}
            >
          <Paper>
              <Text darkMode={darkMode}  >
             {t("_Get_Career_")}
              </Text>
            </Paper>
            <Paper>
              <Paper flexName='flexible aCenter ' >
                <Icon name='checked'/>
                <Text darkMode={darkMode} className='medium checkText'>Web development</Text>
              </Paper>
              <Paper flexName='flexible aCenter ' >
                <Icon name='checked'/>
               <Text darkMode={darkMode} className='medium checkText'>Mobile development</Text>
              </Paper>
              <Paper flexName='flexible aCenter' >
                <Icon name='checked'/>
                <Text darkMode={darkMode}  className='medium checkText'>QA manual</Text>
              </Paper>
              <Paper flexName='flexible aCenter' >
                <Icon name='checked'/>
                <Text darkMode={darkMode} className='medium checkText'>QA automation</Text>
              </Paper>
              <Paper flexName='flexible aCenter' >
                <Icon name='checked'/>
                <Text darkMode={darkMode} className='medium checkText'>UX/UI</Text>
              </Paper>
            </Paper>
          </InfoCard>
          </Paper>
        </Paper>
        <Paper className='academySlider'>
          <Paper flexName=' jCenter'>
          <Paper flexName='flexible jCenter'>
            <Text darkMode={darkMode} className='doubleExtraLarge academySliderTitle'> { t("_Successed_")}</Text>
            </Paper>
            <Paper className="CarouselContainer">
            <Slider {...carouselSettings} >
              {successedStudents}
            </Slider>
          </Paper>
          </Paper>
        </Paper>
      </Paper>
    </Paper>
  )
};

export default connect(mapStateToProps, {
  fetchCourses,
  fetchStudents,
})(Academy);
