import React, { useState, useMemo } from 'react';
import { connect } from 'react-redux';
import Slider from "react-slick";

import { FacebookShareButton } from "react-share";

import { MAIN_URL } from 'configs';

import {
  Icon,
  Text,
  Paper,
  TeamMemberCard
} from 'components';

import { useMount } from 'utils';
import { fetchCourse } from 'actions';
import { useTranslator } from 'utils/translator';
import HelmetContent from 'helpers/HelmetContent';

import CourseModal from './CourseModal'
import carouselSettings from './sliderSettings';

import './style.scss';

const mapStateToProps = ({ darkMode, course }) => ({ darkMode, course });

const Course = ({
  course,
  darkMode,
  fetchCourse,
  name,
  match: { params },
}) => {

  const { t } = useTranslator();
  const [openModal, setOpenModal] = useState(false)

  const toggleModal = () => {
    setOpenModal(!openModal)
  };

  useMount(() => {
    fetchCourse(params.id);
  });

  const trainersData = useMemo(() => {
    if (!course || !course.data || !course.data.trainers || !course.data.trainers.length) return null;

    return course.data.trainers.map(({ _id, image_url }) => (
      <Paper className="trainers-item" flexName=' flexible jCenter' key={_id}>
        <TeamMemberCard
          img={image_url}
        />
      </Paper>
    ))
  }, [course]);

  const courseTopics = useMemo(() => {
    if (!course || !course.data || !course.data.topics || !course.data.topics.length) return null;

    return course.data.topics.map((item, key) => (
      <li key={key}>
        <Text darkMode={darkMode} className='medium'>{item}</Text>
      </li>
    ))
  }, [course, darkMode]);

  return (
    <section className='Course'>
      <HelmetContent page="academy"/>
      <Paper
        style={course && course.data && { backgroundImage: `url(${course.data.image_url})` }}
        className='image-content'
      />
      <Paper className='page-content page-padding content' flexName='flexible vertical'>
        <Paper className='title' flexName='flexible jCenter'>
          <Text darkMode={darkMode} className='tripleExtraLarge'> {course && course.data && course.data.name}</Text>
        </Paper>
        <Paper className='info' flexName='flexible jBetween'>
          <Paper className='left'>
            <Paper className='flexible vertical text-parent'>
              <Text darkMode={darkMode} className='about-title extraLarge'>
                {t('_Description_')}
              </Text>
              <Text darkMode={darkMode} className='medium'>
                {course && course.data && course.data.description}
              </Text>
            </Paper>
            <Paper className='flexible vertical text-parent'>
              <Text darkMode={darkMode} className='about-title extraLarge'>
                {t('_WhatYouLearn_')}
              </Text>
              <ul>
                {courseTopics}
              </ul>
            </Paper>
            <Paper className=' text-parent'>
              {/*<Button onClick={toggleModal} className='left-button'> {t('_ApplyNow_')} </Button>*/}
            </Paper>
          </Paper>
          <Paper className='right' flexName=' flexible vertical jBetwen'>
            <Paper className='slider-content' >
              <Slider {...carouselSettings}>
                {trainersData}
              </Slider>
            </Paper>
            <Paper className='trainer-content' flexName='flexible '>
              <Paper className='futures' flexName='flexible aStart vertical'>
                <Text darkMode={darkMode} className='extraLarge'>{t('_CourseFeatures_')}</Text>
                <Paper>
                  <ul>
                    <li className='prices'>
                      <Text darkMode={darkMode} className='medium bold'>{t('_Duration_')}</Text>
                      <Text darkMode={darkMode} className='medium'> - {course && course.data && course.data.duration} {t('_Month_')}</Text>
                    </li>
                    <li className='prices'>
                      <Text darkMode={darkMode} className='medium bold'>{t('_Lessons_')}</Text>
                      <Text darkMode={darkMode} className='medium'> - {course && course.data && course.data.lessons_count}</Text>
                    </li>
                    <li className='prices'>
                      <Text darkMode={darkMode} className='medium bold'>{t('_FeePerPhase_')}</Text>
                      <Text darkMode={darkMode} className='medium'> - {course && course.data && course.data.price}֏</Text>
                    </li>
                  </ul>
                </Paper>
              </Paper>
            </Paper>
            <Paper className="share-block" flexName="flexible aCenter">
              <Text darkMode={darkMode}>
                {t('_Share_')}
              </Text>
              <FacebookShareButton url={`${MAIN_URL}academy/course/${params.id}`}>
                <Icon name='facebook' className='icons' />
              </FacebookShareButton>
            </Paper>
            <Paper>
            <Paper>
              {/*<Button className='right-button'>{t('_ApplyNow_')}</Button>*/}
            </Paper>
            </Paper>
          </Paper>
        </Paper>
        <Paper className="CourseModal">
          {
            openModal && <CourseModal name={name} openModal={openModal} closeModal={toggleModal} />
          }
        </Paper>
      </Paper>
    </section>
  )
};

export default connect(mapStateToProps, {
  fetchCourse,
})(Course);
