import React, { useMemo } from 'react';
import classnames from 'classnames';

import { Image } from 'components';

import Carousel from './Carousel';

const settings = {
  dots: false,
  infinite: true,
  autoplay: true,
  arrows: false,
  speed: 300,
  slidesToShow: 3,
  slidesToScroll: 1,
  responsive: [
   {
     breakpoint: 1024,
     settings: {
       slidesToShow: 3,
       slidesToScroll: 1,
     },
   },
   {
     breakpoint: 820,
     settings: {
       slidesToShow: 3,
       slidesToScroll: 1,
     },
   },
   {
     breakpoint: 580,
     settings: {
       slidesToShow: 2,
       slidesToScroll: 1,
     },
   },
   {
     breakpoint: 420,
     settings: {
       slidesToShow: 1,
       slidesToScroll: 1,
     },
   },
 ],
}

const SponsorsCarousel = ({ darkMode, partners }) => {

  const partnersContent = useMemo(() => {
    if(!partners || !partners.data || !partners.data.result || !partners.data.result.length) return null;

    return partners.data.result.map(item => (
      <a
        target="_blank"
        rel="noopener noreferrer"
        className={classnames('partners-item', { 'darkMode': darkMode })}
        href={item.websiteUrl}
        key={item._id}
        title={item.name}
      >
        <Image
          src={item.image_url}
          className='sponsorsImage'
        />
      </a>
    ));
  }, [partners, darkMode]);

  return (
     <Carousel settings={settings}>
        {partnersContent}
      </Carousel>
  );
}

export default SponsorsCarousel;
