import React from 'react';

import { connect } from 'react-redux';

import {
    Text,
    Modal,
    Paper,
    Button,
    TextField,
} from 'components';

import './style.scss';

const mapStateToProps = ({ darkMode, course }) => ({ darkMode, course});

const CourseModal = ({
  name,
  openModal,
  closeModal,
}) => (openModal && (
    <Modal
        closeModal={closeModal}
    >
        <Paper className="carouselModalContent">
            <Paper flexName="flexible jCenter" className="title-container">
                <Text className="modalTitle">{name}</Text>
            </Paper>
            <TextField className="modalInput" placeholder='name' />
            <TextField className="modalInput" placeholder='surname' />
            <TextField className="modalInput" placeholder='phone' />
            <TextField className="modalInput" placeholder='email' />
            <Paper flexName="flexible jCenter">
                <Button className="orange modalButton" size='small'>Senc Massage</Button>
            </Paper>
        </Paper>
    </Modal>
));

export default connect(mapStateToProps,null)(CourseModal);
