import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import {
  Route,
  Switch,
  withRouter,
  Redirect,
} from 'react-router-dom';
import classnames from 'classnames';

import {
  Main,
  Blog,
  Course,
  AboutUs,
  Carrers,
  Academy,
  Services,
  ContactUs,
  BlogNews
} from 'containers';

import {
  Header,
  Footer,
  DarkModeButton
} from 'components';
import { useMount } from 'utils';
import { fetchWebsiteSettings } from 'actions';

import { toggleDarkMode } from 'actions';

import 'sass/animate.scss';
import 'sass/main.scss';
import 'sass/media.scss';

const mapStateToProps = ({ darkMode }) => ({ darkMode });

const App = ({
  darkMode,
  toggleDarkMode,
  fetchWebsiteSettings,
}) => {
  useMount(() => {
    fetchWebsiteSettings();
  });

  useEffect(() => {
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
        toggleDarkMode(true);
    };
  },[toggleDarkMode]);

  return (
    <>
      <Header darkMode={darkMode} />
        <main className={classnames("Main", { 'darkMode': darkMode })}>
          <DarkModeButton
            active={darkMode}
            onClick={toggleDarkMode}
          />
          <Switch>
              <Route exact path="/" component={Main} />
              <Route exact path="/blog" component={Blog} />
              <Route path="/blog/:id?" component={BlogNews} />
              <Route exact path="/academy" component={Academy} />
              <Route path="/academy/course/:id?" component={Course} />
              <Route path="/careers/:id?" component={Carrers} />
              <Route path="/about-us" component={AboutUs} />
              <Route path="/services" component={Services} />
              <Route path="/contact-us" component={ContactUs} />
              <Redirect from='/' to='/'/>
          </Switch>
        </main>
      <Footer/>
    </>
  )
};

export default connect(mapStateToProps, {
  toggleDarkMode,
  fetchWebsiteSettings,
})(withRouter(App));
