import React, { useMemo } from 'react';

import { connect } from 'react-redux';
import classnames from 'classnames';

import {
  Icon,
  Text,
  Paper,
  Image,
  Collapse,
  ImageContent,
} from 'components';

import { useMount } from 'utils';
import { fetchCarrers } from "actions";
import { useTranslator } from 'utils/translator';
import HelmetContent from 'helpers/HelmetContent';


import {
  careersImage,
  careersImageDark,
  careersBoyImage,
  carrersGirlImage,
} from 'assets/pages';
import face from 'assets/svg/face.svg';
import team from 'assets/svg/team.svg';

import "./style.scss";

const mapStateToProps = ({ darkMode, careers }) => ({ darkMode, careers });

const Carrers = ({ darkMode, fetchCarrers, careers }) => {

  const { t } = useTranslator();

  useMount(() => {
    fetchCarrers();
  });

  const generateCollapse = useMemo(() => {
    if (!careers || !careers.data || !careers.data.result) return null;

    return careers.data.result.length ? (
      <Collapse
        darkMode={darkMode}
        data={careers.data.result}
        keyLabel="_id"
      />
    ) : (
        <Paper className="cv-container">
          <Paper className="cv-block">
            <Text className="sorry-we-currently" darkMode={darkMode}>
              {t('_SorryWeCurrently_')}
              <Text className="levelUp_gmail" darkMode={darkMode}>leveluparmenia@gmail.com</Text>
            </Text>
            <Paper className="iamge-block">
              <Image
                width={173}
                height="auto"
                src={face}
                alt={face}
              />
            </Paper>
          </Paper>
        </Paper>
      )
  }, [careers, darkMode, t]);



  return <div className="Carrers">
    <HelmetContent page='careers'/>
    <ImageContent
      path={careersImage}
      before={carrersGirlImage}
      after={careersBoyImage}
      darkPath={careersImageDark}
      darkMode={darkMode}
    />
    <Paper className={classnames('Carrers', { 'darkMode': darkMode })}>
      <section className="carrers-main page-content">
        <Paper className="carrers-content">
          <Paper className="title-carrers flexible jCenter">
            <Text darkMode={darkMode} className="doubleExtraLarge">{t('_WhyJoinOurTeam_')}</Text>
          </Paper>
          <Paper className="info-block-container flexible center jCenter">
            <Paper className="infoBlockSides" flexName="flexible vertical jBetween">
              <Paper className="infoBlockText" flexName="flexible">
                <Icon name="oval" />
                <Paper>
                  <Text darkMode={darkMode} className="heading-2 extraLarge">{t('_EnglishClub_')}</Text>
                  <Paper>
                    <Text className="text-content" darkMode={darkMode}>
                      {t('_EnglishClubText_')}
                    </Text>
                  </Paper>
                </Paper>
              </Paper>
              <Paper className="infoBlockText" flexName="flexible">
                <Icon name="oval" />
                <Paper>
                  <Text darkMode={darkMode} className="heading-2 extraLarge">{t('_GrowingTeam_')}</Text>
                  <Paper>
                    <Text className="text-content" darkMode={darkMode}>
                      {t('_GrowingTeamText_')}
                    </Text>
                  </Paper>
                </Paper>
              </Paper>
            </Paper>
            <Paper className="infoBlockImage">
              <Image
                width={600}
                height="auto"
                src={team}
                alt={team}
              />
            </Paper>
            <Paper className="infoBlockSides" flexName="flexible vertical jBetween">
              <Paper className="infoBlockText" flexName="flexible">
                <Icon name="oval" />
                <Paper>
                  <Text darkMode={darkMode} className="heading-2 extraLarge">{t('_FilmClub_')}</Text>
                  <Paper>
                    <Text className="text-content" darkMode={darkMode}>
                      {t('_FilmClubText_')}
                    </Text>
                  </Paper>
                </Paper>
              </Paper>
              <Paper className="infoBlockText lastInfoBlock" flexName="flexible">
                <Icon name="oval" />
                <Paper>
                  <Text darkMode={darkMode} className="heading-2 extraLarge">{t('_GameClub_')}</Text>
                  <Paper>
                    <Text className="text-content" darkMode={darkMode}>
                      {t('_GameClubText_')}
                    </Text>
                  </Paper>
                </Paper>
              </Paper>
            </Paper>
          </Paper>
          <Paper className="title-carrers flexible jCenter">
            <Text darkMode={darkMode} className="doubleExtraLarge">{t('_OpenPositions_')}</Text>
          </Paper>
          {generateCollapse}
        </Paper>
      </section>
    </Paper>
  </div>
};

export default connect(mapStateToProps, {
  fetchCarrers,
})(Carrers);
