import React from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';

import {
  Paper,
  ImageContent,
  ServiceInfoCard
} from 'components';

import { useTranslator } from 'utils/translator';
import HelmetContent from 'helpers/HelmetContent';

import {
  servicesImage,
  servicesImageDark,
  servicesBoyImage,
  servicesGirlImage,
} from 'assets/pages';

import './style.scss';

const mapStateToProps = ({ darkMode }) => ({ darkMode });

const Services = ({ darkMode }) => {

  const {t} = useTranslator();

  const infoData = [
    {
      iconName: 'screen',
      title: t("_Web_") + ' ' + t("_Development_"),
      desc: t("_Web_Development_Text_"),
      key: 0,
    }, {
      iconName: 'mobile',
      title: t("_Mobile_") + ' ' + t("_Development_"),
      desc: t("_Mobile_Development_Text_"),
      key: 1,
    }, {
      iconName: 'pencil',
      title: t("_UX/UIDesign_"),
      desc: t("_UX/UIDesign_Text_"),
      key: 2,
    }, {
      iconName: 'search',
      title: t("_Teaching_"),
      desc: t("_Teaching_Text_"),
      key: 3,
    },
  ];

  return (
        <Paper flexName="flexible wrap jCenter" className={classnames('Services')}>
          <HelmetContent page="services" />
          <ImageContent
            path={servicesImage}
            before={servicesBoyImage}
            after={servicesGirlImage}
            darkPath={servicesImageDark}
            darkMode={darkMode}
          />
          <section className="categoriesList Paper page-content flexible grow jBetween wrap">
            {infoData.map(({ title, key, desc, iconName }) =>
                <ServiceInfoCard
                  key={key}
                  className={`animated ${key%2 ? 'fadeInRight' : 'fadeInLeft'}`}
                  darkMode={darkMode}
                  iconName={iconName}
                  title={title}
                  desc={desc}
                />)
             }
          </section>
        </Paper>
      );
};

export default connect(mapStateToProps, null)(Services);
