import React, { useMemo, useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom'
import classnames from 'classnames';

import { useMount } from 'utils';
import { fetchBlogItems } from 'actions';
import HelmetContent from 'helpers/HelmetContent';

import {
  Paper,
  BlogCard,
  Pagination,
  ImageContent,
} from 'components';

import {
  blogImage,
  blogImageDark,
  blogBoyImage,
  blogGirlImage,
} from 'assets/pages';

import './style.scss';

const mapStateToProps = ({ darkMode, blogItems }) => ({ darkMode, blogItems });

const Blog = ({
  darkMode,
  fetchBlogItems,
  blogItems
}) => {
  const [page, setPage] = useState(1);

  useMount(() => {
    fetchBlogItems(page);
  });

  useEffect(() => {
    fetchBlogItems(page);
  }, [page, fetchBlogItems]);

  const generateBlogItems = useMemo(() => {
    if(!blogItems || !blogItems.data || !blogItems.data.result || !blogItems.data.result.length) return null;

    return blogItems.data.result.map((item, index) => (
      <Paper className='blogitems' key={item._id}>
        <NavLink to={`blog/${item._id}`}>
          <BlogCard
            title={item.name}
            description={item.description}
            createdDate={item.createdDate}
            views={item.views}
            src={item.image_url}
            darkMode={darkMode}
          />
        </NavLink>
      </Paper>
    ))
  }, [blogItems, darkMode]);

  return (
    <Paper className={classnames("Blog", { 'darkMode': darkMode })}>
      <HelmetContent page="blog"/>
      <ImageContent
        path={blogImage}
        before={blogGirlImage}
        after={blogBoyImage}
        darkPath={blogImageDark}
        darkMode={darkMode}
      />
      <section className='blogNews'>
        <Paper className='page-content blogComments' flexName='flexible wrap jAround'>
          {generateBlogItems}
        </Paper>
      </section>
      <section>
        <Pagination
          darkMode={darkMode}
          count={(blogItems && blogItems.data) && (blogItems.data.count || 0)}
          onPageChange={setPage}/>
      </section>
    </Paper>
  )
};
export default connect(mapStateToProps, {
  fetchBlogItems,
})(Blog);
