import React from 'react';
import {
    Map,
    Marker,
    GoogleApiWrapper,
} from 'google-maps-react';

import styles from './darkmodeStyle';

import logo from 'assets/svg/mapIcon.svg';

const MapContainer = ({
  google,
  darkMode,
  office_lat,
  office_lng,
  google_map_apiKey
}) =>  (
  <Map
    google={google}
    zoom={17}
    key={darkMode}
    initialCenter={{
        lat: office_lat,
        lng: office_lng
    }}
    style={{
        height: '432px'
    }}
    styles={darkMode ? styles : []}
  >
    <Marker
      name={'Current location'}
      icon={{ url: logo }}
    >
    </Marker>
  </Map>
);


export default GoogleApiWrapper(({ google_map_apiKey }) => ({
  apiKey: google_map_apiKey
}))(MapContainer);
