import React, { useMemo } from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';
import Slider from "react-slick";

import { useMount } from 'utils';
import { fetchTeamMembers } from 'actions';
import { useTranslator } from 'utils/translator';
import HelmetContent from 'helpers/HelmetContent';

import {
  Text,
  Paper,
  InfoCard,
  ImageContent,
  TeamMemberCard,
} from 'components';

import {
  aboutUsImage,
  aboutUsBoyImage,
  aboutUsGirlImage,
  aboutUsImageDark,
} from 'assets/pages';

import config from './sliderSettings';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./style.scss"

const { dataForAboutUs, settings } = config;

const generateInfoData = (t, darkMode) => (
  dataForAboutUs.map(item => ({
    ...item,
    darkMode,
    text: t(item.text),
    title: t(item.title),
  }))
);

const mapStateToProps = ({ darkMode, teamMembers }) => ({ darkMode, teamMembers });

const AboutUs = ({
  darkMode,
  teamMembers,
  fetchTeamMembers,
}) => {

  useMount(() => {
    fetchTeamMembers();
  });

  const { t } = useTranslator();

  const infoData = useMemo(() => generateInfoData(t, darkMode), [t, darkMode]);

  const generateTeamMembers = useMemo(() => {
    if(!teamMembers || !teamMembers.data || !teamMembers.data.result || !teamMembers.data.result.length) return null;

    return teamMembers.data.result.map((item) => (
      <Paper className="carouselItem" key={item._id}>
        <TeamMemberCard
          darkMode={darkMode}
          img={item.image_url}
          name={item.name}
          surname={item.surname}
          profession={item.profession}
        />
      </Paper>
    ));
  },[teamMembers,darkMode ]);

  const infoDataContent = useMemo(() => (
    infoData && infoData.map((item, index) => (
        <section key={index}>
          <InfoCard
            reverse={item.reverse}
            img={item.img}
            title={item.title}
            text={item.text}
            className={item.className}
            darkMode={darkMode}
            color={item.color || ''}
            backgroundColor={item.backgroundColor || ''}
          />
        </section>
    ))
  ), [infoData, darkMode]);

  return (
    <Paper className={classnames("AboutUs", { 'darkMode': darkMode })}>
      <HelmetContent page="aboutUs"/>
      <ImageContent
        path={aboutUsImage}
        before={aboutUsBoyImage}
        after={aboutUsGirlImage}
        darkPath={aboutUsImageDark}
        darkMode={darkMode}
      />
      {infoDataContent}
      <section className='AboutUs flexible jCenter grow'>
        <Paper className='page-content aboutUsBlockSlider'>
          <Paper flexName='flexible  jCenter'>
          <Text darkMode={darkMode} className='doubleExtraLarge teamMemberTitle '> {t("_OurTeam_")}</Text>
          </Paper>
          <Paper className="teamMembersCarouselContainer">
            <Slider {...settings} >
                {generateTeamMembers}
            </Slider>
          </Paper>
        </Paper>
      </section>
    </Paper>
  )
}
export default connect(mapStateToProps, {fetchTeamMembers})(AboutUs);
