import React from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';

import {
  Icon,
  Text,
  Paper,
  Button,
  TextField,
  ImageContent,
} from 'components';

import {
  contactUsImage,
  contactUsImageDark,
  contactUsBoyImage,
  contactUsGirlImage,
} from 'assets/pages';

import {
   useTranslator
} from 'utils/translator';
import HelmetContent from 'helpers/HelmetContent';

import Map from "./Map";

import "./style.scss";

const mapStateToProps = ({ darkMode ,website_settings }) => ({ darkMode,  website_settings});

const ContactUs = ({
   darkMode ,
   website_settings
  }) => {

  const { t } = useTranslator();

  const {
    office_address,
    office_phone,
    office_mail,
    office_website,
    facebook,
    instagram,
    linkedin,
    google_map_apiKey,
    office_lat,
    office_lng
  } = website_settings ? website_settings.data.settings : {};

  return (
    <Paper flexName='flexible wrap' className={classnames("ContactUs", { 'darkMode': darkMode })}>
      <ImageContent
        path={contactUsImage}
        before={contactUsBoyImage}
        after={contactUsGirlImage}
        darkPath={contactUsImageDark}
        darkMode={darkMode}
      />
      <HelmetContent page="contactUs" />
      <section className='registerInfoField flexible jCenter grow'>
        <Paper flexName='flexible wrap grow' className='page-content content-media'>
          <Paper flexName='flexible vertical' className='contactUsForm animated fadeInUp'>
            <Text darkMode={darkMode} className='extraLarge contactUsFormText'> {t('_GetInTouch_')}  </Text>
            <Paper flexName='flexible' className='contactUsFormTextFields' >
              <TextField
                placeholder={t("_Name_")}
                size="medium"
                darkMode={darkMode}
              />
              <TextField
                placeholder={t("_Surname_")}
                size="medium"
                darkMode={darkMode}
              />
            </Paper>
            <Paper flexName='flexible' className='contactUsFormTextFields'>
              <TextField
                placeholder={t("_Email_")}
                size="medium"
                darkMode={darkMode}
              />
              <TextField
                placeholder={t("_Phone_")}
                type="number"
                size="medium"
                darkMode={darkMode}
              />
            </Paper>
            <Paper className='textarea'>
              <TextField className='area' darkMode={darkMode} textarea placeholder={t("_TextAreaText_")} />
            </Paper>
            <Paper className='flexible jEnd send-button'>
              <Button> {t("_SendMassage_")}</Button>
            </Paper>
          </Paper>
          <Paper flexName='flexible wrap vertical' className='contactUsInfo animated fadeInUp'>
            <Paper className='contactUsInfoContent'>
              <Text darkMode={darkMode} className='extraLarge contactUsInfoText'> {t('_ContactUs_')} </Text>
              <Paper flexName='flexible vertical'>
                <Paper flexName='flexible aCenter' className='contactsBlock'>
                  <Paper className='infoLocation'>
                      <Icon name='mapBlue' />
                  </Paper>
                  <Paper flexName='flexible vertical' className='mediaText'>
                      <Text darkMode={darkMode} className='large'>{office_address}</Text>
                  </Paper>
                </Paper>
                <Paper flexName='flexible aCenter' className='contactsBlock'>
                  <Paper className='infoLocation'>
                      <Icon name='telephoneBlue' />
                  </Paper>
                  <Paper flexName='flexible vertical' className='mediaText'>
                     <Text darkMode={darkMode} className='large'>{office_phone}</Text>
                  </Paper>
                </Paper>
                <Paper flexName='flexible aCenter' className='contactsBlock'>
                  <Paper className='infoLocation'>
                      <Icon name='envelopeBlue' />
                  </Paper>
                  <Paper flexName='flexible vertical' className='mediaText'>
                    <Text darkMode={darkMode} className='large'>{office_mail}</Text>
                  </Paper>
                </Paper>
                <Paper flexName='flexible aCenter' className='contactsBlock'>
                  <Paper className='infoLocation'>
                      <Icon name='globus' />
                  </Paper>
                  <Paper flexName='flexible vertical' className='mediaText'>
                      <Text darkMode={darkMode} className='large'>{office_website}</Text>
                  </Paper>
                </Paper>
                <Paper flexName='flexible aCenter' className='socialBlock'>
                  <Paper className='socialBlockText'>
                      <Text darkMode={darkMode} className='large' > {t("_FollowUs_")}</Text>
                  </Paper>
                  <Paper flexName='flexible'>
                    <a href={facebook} target="_blank" rel="noopener noreferrer">  <Icon name='facebook' /></a>
                    <a href={instagram} target="_blank" rel="noopener noreferrer"> <Icon name='instagram' /> </a>
                    <a href={linkedin} target="_blank" rel="noopener noreferrer"> <Icon name='linkedin' /> </a>
                  </Paper>
                </Paper>
              </Paper>
            </Paper>
          </Paper>
        </Paper>
      </section>
      <section className='contactUsMap'>
        { website_settings && <Map darkMode={darkMode} google_map_apiKey={google_map_apiKey}  office_lat={office_lat} office_lng={office_lng} />}
      </section>
    </Paper>
  )
};

export default connect(mapStateToProps,null)(ContactUs);
