import React from 'react';
import { connect } from 'react-redux';
import classnames from 'classnames';

import {
  FacebookShareButton,
  LinkedinShareButton,
} from "react-share";

import {
  Icon,
  Text,
  Paper,
} from "components";

import { useMount } from 'utils';
import { MAIN_URL } from 'configs';
import { fetchBlogItem } from 'actions';
import HelmetContent from 'helpers/HelmetContent';

import "./style.scss"

const mapStateToProps = ({ darkMode, blogItem }) => ({ darkMode, blogItem });

const BlogNews = ({
  blogItem,
  darkMode,
  fetchBlogItem,
  match: { params },
}) => {

  useMount(() => {
    params.id && fetchBlogItem(params.id);
  });

  return (
    <Paper className={classnames("BlogNews", { 'darkMode': darkMode })}>
      <section>
        <HelmetContent page="blog"/>
        <Paper className='page-content'>
          <Paper
            className="blog-image"
            style={{ backgroundImage: blogItem && blogItem.data && `url(${ blogItem.data.image_url})` }}
          />
          <Paper className="blog-content">
            <Text darkMode={darkMode} className='tripleExtraLarge blog-name'>{blogItem && blogItem.data && blogItem.data.name}</Text>
            <div
              className="blog-body"
              dangerouslySetInnerHTML={{
                __html: blogItem && blogItem.data && blogItem.data.content && blogItem.data.content.body
              }}
            />
          </Paper>
          <Paper className='shareBlock' flexName='flexible aCenter'>
             <Text darkMode={darkMode} className='medium shareText'>Share this</Text>
             <Paper className='iconBlock'>
                <FacebookShareButton url={`${MAIN_URL}blog/${params.id}`}>
                  <Icon name='facebook' className='icons' />
                </FacebookShareButton>
                <LinkedinShareButton url={`${MAIN_URL}blog/${params.id}`}>
                  <Icon name='linkedin' className='icons' />
                </LinkedinShareButton>
             </Paper>
          </Paper>
        </Paper>
      </section>
    </Paper>
  );
};

export default connect(mapStateToProps, {
  fetchBlogItem
})(BlogNews);
