import React, { useMemo } from 'react';

import {  Testimonials } from 'components';

import Carousel from './Carousel';

const settings = {
   dots: false,
   infinite: true,
   speed: 300,
   slidesToShow: 3,
   slidesToScroll: 1,
   arrows: false,
   autoplay: true,
   responsive: [
     {
       breakpoint: 1180,
       settings: {
         slidesToShow: 2,
         slidesToScroll: 1,
       },
     },
    {
      breakpoint: 790,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
};

const TestimonialsCarousel = ({
  darkMode,
  testimonials,
}) => {

  const testimonialsContent = useMemo(() => {
    if(!testimonials || !testimonials.data || !testimonials.data.result || !testimonials.data.result.length) return null;

    return testimonials.data.result.map(item => (
      <Testimonials
        darkMode={darkMode}
        key={item._id}
        name={item.name}
        surname={item.surname}
        description={item.description}
        profession={item.profession}
        src={item.image_url}
        size={100}
        className="koko"
      />
    ));
  }, [testimonials, darkMode]);

  return testimonialsContent && (
      <Carousel settings={settings}>
        {testimonialsContent}
      </Carousel>
    );
};

  export default TestimonialsCarousel;
